"use client";

import Link from "next/link";
import Image from "next/image";
import favicon from "../app/favicon.ico";
// import { useAuthStore } from "../stores/useAuthStore.js";
import { User2Icon, MenuIcon, WalletIcon } from "lucide-react"; 
import { motion } from "framer-motion";
import { useState } from "react";

const Header = () => {
  // const { user, logout } = useAuthStore();
  const [openNavigation, setOpenNavigation] = useState(false);

  const toggleNav = () => {
    setOpenNavigation(!openNavigation);
  };
 
  

  return (
    <header
      className={`bg-[#08021b] shadow-lg px-2 lg:px-4 fixed left-0 top-0 z-50 min-h-[7rem] w-full text-white bg-opacity-90 backdrop-blur-sm transition-all duration-300 border-b border-b-black ${
        openNavigation ? "bg-[#08021b]" : "opacity-95 backdrop-blur-sm"
      } `}
    >
      <motion.div className="container mx-auto px-4 py-2 " initial={{ opacity: 0, y: 20 }}
						animate={{ opacity: 1, y: 0 }}
						transition={{ duration: 0.5, delay: 0.2 }}
					>
        <div className="flex flex-wrap justify-between items-center">
          <Link href="/">
            <Image
              src={grande_money_logo}
              alt="logo"
              className={
                "hover:opacity-85 transition-opacity opacity-100 rounded size-[6rem] "
              }
            />
          </Link>

          <div>
            <button
              className="text-black bg-white cursor-pointer ml-auto lg:hidden"
              onClick={toggleNav}
              aria-label={
                openNavigation ? "Close Navigation" : "Open Navigation"
              }
              aria-expanded={openNavigation}
            >
              <MenuIcon className="p-0.5" size={35} />
            </button>
          </div>

          <nav
            className={`flex flex-col lg:flex-row items-center gap-5 ${
              openNavigation ? "flex" : "hidden"
            } fixed top-[6rem] left-0 right-0 bottom-0 lg:static lg:flex ml-auto lg:gap-6 xl:gap-8 z-2 bg-[#08021b] backdrop-blur-sm max-lg:h-[20rem] `}
          > 
            {user? (<>
                <Link
                  href="/profile"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Profile
                </Link>
                <Link
                  href="/dashboard"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Dashboard
                </Link>
                <Link
                  href="/settings"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Settings
                </Link>
                <Link
                  href="/support"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Support
                </Link>
                <button
                  className="px-4 py-2 bg-pink-500 border-pink-600 text-white font-semibold rounded-lg cursor-pointer flex items-center space-x-2 transition-all hover:text-black duration-300 ease-in-out  neon-glow-button hover:scale-105 hover:border-pink-400 focus:outline-none hover:bg-pink-500 focus:ring-2 focus:ring-pink-500 focus:ring-offset-2 focus:ring-offset-gray-900"
                >
                  <span className="sm:inline">Log Out</span>
                </button>
                <Link href={"/checkout"} className="text-black bg-white rounded-bl-2xl rounded-tr-2xl cursor-pointer" >
                  <WalletIcon className="p-1" size={35} />
                </Link>
              </>): (<>
                <Link
                  href="#why-choose-us"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Why GeM AI?
                </Link>
                <Link
                  href="#features"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Features
                </Link>
                <Link
                  href="#pricing"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Pricing
                </Link>
                <Link
                  href="/support"
                  className="text-gray-300 transition-hover hover:text-white font-mona-sans "
                >
                  Support
                </Link>
                <Link
                  href="/signup"
                  className="px-4 py-3 bg-pink-500 border-pink-600 text-white font-semibold rounded-lg cursor-pointer flex items-center transition-all hover:text-black duration-300 ease-in-out  neon-glow-button hover:scale-105 hover:border-pink-400 focus:outline-none hover:bg-pink-500 focus:ring-2 focus:ring-pink-500 focus:ring-offset-2 focus:ring-offset-gray-900"
                >
                  <User2Icon className="mr-2" size={18} />
                  <span>Signup</span>
                </Link>

                <Link
                  href={"/login"}
                  className="px-5 py-[13px] transform  hover:scale-105 border-pink-500/60 text-white font-semibold rounded-lg bg-pink-500/50 hover:bg-pink-500/60 transition-all duration-300 flex items-center space-x-2 cursor-pointer"
                >
                  Login
                </Link>
              </>)}
          </nav>
        </div>
      </motion.div>
    </header>
  );
};

export default Header;
